@import "fonts";

$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: "Abraham";

$blue: #4D58C3;
$pink: #C14DC3;
$red: #d03854;
$green: #4dc367;
$light-blue: #65a6d6;

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html{
	scroll-behavior: smooth;
	font-size: 62.5%;
	overflow-x: hidden;
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;

}


nav{
	position: fixed;
	z-index: 5;
	width: 100%;
	top: 0;
	background: $blue;

	> div:first-child{
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 5vw 0 5vw;
		padding: .5rem 0 0 0;
		// position: relative;
		button{
			background: none;
			border: none;
			span{
				display: block;
				height: 3px;
				width: 30px;
				background: white;
				margin: 5px 0;
				border-radius: 1px;
			}

		}
	}

	img{
		width: 50%;
	}

}
nav + div{
	// transform:scale(0);
	font-size: 2rem;
	height: 0px;
	// opacity: 0;
	display: flex;
	flex-direction: column;
	position: fixed;
	background: $blue;
	z-index: 2;
	width: 100%;
	text-align: center;
	a{
		// height: 0px;
		// display: none !important;
		// display: none;
		opacity: 0;
		pointer-events: none;
		// transform: scale(0);
		padding: 1rem 0;
		transition: opacity 100ms;

	}
	transition: transform 300ms, height 300ms;
	transform-origin: top;
	// display: none;
}

nav + div.show{
	transform: scale(1);
	// position: relative;
	// opacity: 1;
	height: 30%;
	// display: flex;
	a{
		transition: opacity 300ms  200ms;
		// height: 100%;
		transform: scale(1);
		opacity: 1;
		pointer-events: all;
		// display: block;
	}
}
a{
	color: white;
	text-decoration: none;
}

section{
	display: flex;
	flex-direction: column;
	position: relative;
	color: white;


	h1{
		font-size: 3rem;
		margin-bottom: 1rem;
	}
	p{
		font-size: 2rem;
		line-height: 1.5;
		margin-bottom: 2rem;
	}
}


section#about{
	background-color: $blue;
	padding: 2rem 2rem;


	img{
		width: 60%;
		align-self: center;
	}
}
section#meshutaf,
section#social-justice,
section#kibbutzi,
section#nachshonim,
section#hero,
section#contact
{
	> div{
		padding: 2rem 2rem 0 2rem;
		width: 100%;
		display: flex;
		flex-direction: column;

		img{
			width: 120%;
			align-self: center;
			transform: translateY(10%);
			z-index: 0;
		}
	}
	div + img{
		width: 110%;
		margin-top: -50%;
		align-self: baseline;
		z-index: 1;
		// position: absolute;
		// bottom: 0;
		// right: 0;
	}
}

section#hero{
		margin-top: 4rem;
	div{
		padding-bottom: 5px;
		padding-top: 5px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		// padding-top: 0;
		button{
			background: $blue;
			padding: 1rem;
			font-size: 2rem;
			border: none;
			border-radius: 3px;
			width: 40%;
		}
		p{
			width: 60%;
			margin: .7rem 1rem;
			color: $blue;
			text-align: center;
			white-space: pre;
		}
	}
	video{
		width: 100vw;
	}
}
section#meshutaf	div{
		background-color: $pink;
}

section#social-justice div{
	background-color: $red;
}
section#kibbutzi{
	div{
		background-color: $green;
		position: relative;
		// background: url("/assets/img/svg/mobile/kibbutzi.svg");
		// background-size: cover;
		// background-position: 0 10%;
		padding-bottom: 20%;
		img{
			margin-top: -30%;
			// position: absolute;
			// top: -15%;
			// left: 0;
			width: 120%;
			// margin-top: -83%;
		}
	}
	div + img{
		width: 100%;
		margin-top: -40%;
		// background: white;
		// transform: translateY(10%);
	}
}
section#nachshonim{
	 div{
		background-color: $light-blue;
		img{
			margin-top: -50%;
			width: 110%;
			transform: translateY(50%);
		}
	}
	div + img{
		width: 100%;
		margin-top: -30%;
		margin-left: 15%;
		align-self: center;
	}
}

section#contact{
	// height: 100vh;
	background-color: $blue;
	padding-bottom: 5rem;
	> div:first-child{
		display: grid;
		grid-template-areas:
		"h1 logo"
		"p logo";
		align-items: center;
		// justify-content: center;
		h1{
			grid-area: h1;
			font-weight: bold;
		}
		p{
			grid-area: p;
			font-size: 1.6rem;
		}
		img{
			grid-area: logo;
			align-self: center;
			justify-self: center;
			width: 80%;
			margin-bottom: 30%;
			// margin: auto;
		}
	}
	> div + div{
		background: white;

		[name="submit"],
		button{
			margin: 1rem 0;
			width: 100%;
			border: none;
			color: white;
			background: $blue;
			border-radius: 3px;
			font-weight: 500;
			font-family: "Abraham";
			font-size: 2rem;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 1rem 0;
			img{
				height: 2rem;
			}
		}
		form{
			> div{

				background: $blue;
				padding: 1rem 1rem 2rem 1rem;
				border-radius: 5px;
				display: grid;
				grid-gap: 1rem;
				grid-template-columns: 1fr 1fr;
				grid-template-rows: repeat(4, auto);
				div{
					display: flex;
					flex-direction: column;
					// padding: 1rem;
					label,
					input{
						display: block;
						width: 100%;
						font-size: 1.5rem;
					}
				}
				div:last-child,
				div:nth-last-child(2)
				{
					grid-column: 1 / 3;
				}
			}

		}
		p{
			color: $blue
		}

	}
}
